"use client";
import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

// Subject list
const subjects = [
  { name: "Mathematics", icon: "📐" },
  { name: "Physics", icon: "⚛️" },
  { name: "Chemistry", icon: "🧪" },
];

// Sidebar component with dropdowns for subjects and lessons
function Sidebar({
  subjects,
  expandedSubjects,
  toggleSubject,
  expandedLesson,
  toggleLesson,
}: {
  subjects: { name: string; icon: string }[];
  expandedSubjects: { [key: string]: boolean };
  toggleSubject: (subject: string) => void;
  expandedLesson: string | null;
  toggleLesson: (lesson: string) => void;
}) {
  return (
    <aside className="w-80 bg-white shadow-2xl border-r border-gray-100 px-6 py-8 overflow-y-auto flex flex-col min-h-screen">
      {/* Sidebar Header */}
      <div className="mb-8">
        <h1 className="text-2xl font-bold text-gray-900 mb-2">Study Dashboard</h1>
        <p className="text-gray-600 text-sm">Select a subject to begin learning</p>
      </div>

      {/* Subject Dropdowns */}
      <div className="flex flex-col gap-3">
        {subjects.map((subject) => (
          <div key={subject.name} className="border border-gray-200 rounded-xl overflow-hidden bg-gray-50/50">
            {/* Subject Button */}
            <button
              className={`w-full flex items-center justify-between px-6 py-4 transition-all duration-300 ${
                expandedSubjects[subject.name]
                  ? "bg-gradient-to-r from-blue-600 to-indigo-600 text-white shadow-lg"
                  : "bg-white hover:bg-gray-50 text-gray-800 hover:shadow-md"
              }`}
              onClick={() => toggleSubject(subject.name)}
            >
              <div className="flex items-center gap-3">
                <span className="text-2xl">{subject.icon}</span>
                <span className="font-semibold text-lg">{subject.name}</span>
              </div>
              <svg 
                className={`w-5 h-5 transition-transform duration-300 ${
                  expandedSubjects[subject.name] ? 'rotate-180' : ''
                }`}
                fill="none" 
                viewBox="0 0 24 24" 
                stroke="currentColor"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </button>

            {/* Lessons Dropdown */}
            <AnimatePresence initial={false}>
              {expandedSubjects[subject.name] && (
                <motion.div
                  className="bg-gray-50"
                  initial={{ height: 0, opacity: 0 }}
                  animate={{ height: "auto", opacity: 1 }}
                  exit={{ height: 0, opacity: 0 }}
                  transition={{ duration: 0.3, ease: "easeInOut" }}
                >
                  <div className="px-4 py-2">
                    {[...Array(10)].map((_, i) => {
                      const lesson = `Lesson ${i + 1}`;
                      return (
                        <button
                          key={lesson}
                          className={`w-full text-left px-4 py-3 rounded-lg transition-all duration-200 flex justify-between items-center mb-1 ${
                            expandedLesson === lesson
                              ? "bg-blue-100 text-blue-800 border border-blue-200 font-medium"
                              : "text-gray-700 hover:bg-white hover:text-gray-900 hover:shadow-sm"
                          }`}
                          onClick={() => toggleLesson(lesson)}
                        >
                          <span className="flex items-center gap-2">
                            <div className={`w-2 h-2 rounded-full ${
                              expandedLesson === lesson ? 'bg-blue-500' : 'bg-gray-400'
                            }`}></div>
                            {lesson}
                          </span>
                          {expandedLesson === lesson && (
                            <svg className="w-4 h-4 text-blue-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                            </svg>
                          )}
                        </button>
                      );
                    })}
                  </div>
                </motion.div>
              )}
            </AnimatePresence>
          </div>
        ))}
      </div>

      {/* Footer */}
      <div className="mt-auto pt-6 border-t border-gray-200">
        <p className="text-xs text-gray-500 text-center">
          Study smart, achieve more
        </p>
      </div>
    </aside>
  );
}

// Right panel PDF view with enhanced design
function LessonContent({
  subject,
  lesson,
  expandedLesson,
}: {
  subject: string | null;
  lesson: string | null;
  expandedLesson: string | null;
}) {
  // Always fit to page: we show a "Fit to Page" button that is always visually "on" and do not provide any toggle.
  // The iframe will always be styled to fit the page.
  return (
    <div className="flex-1 bg-gradient-to-br from-gray-50 to-blue-50 overflow-hidden">
      <AnimatePresence mode="wait">
        {subject && lesson && expandedLesson === lesson ? (
          <motion.div
            key={lesson}
            className="h-full flex flex-col p-6"
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
            transition={{ duration: 0.4, ease: "easeOut" }}
          >
            {/* Header */}
            <div className="bg-white rounded-2xl shadow-sm border border-gray-200 p-6 mb-6">
              <div className="flex items-center justify-between">
                <div>
                  <h2 className="text-3xl font-bold text-gray-900 mb-2">
                    {subject}
                  </h2>
                  <p className="text-gray-600 flex items-center gap-2">
                    <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.746 0 3.332.477 4.5 1.253v13C19.832 18.477 18.246 18 16.5 18c-1.746 0-3.332.477-4.5 1.253" />
                    </svg>
                    Currently viewing: <span className="font-semibold text-blue-600">{lesson}</span>
                  </p>
                </div>
                <div className="flex gap-2">
                  <button className="px-4 py-2 bg-blue-100 text-blue-700 rounded-lg hover:bg-blue-200 transition-colors font-medium">
                    Download
                  </button>
                  <button className="px-4 py-2 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200 transition-colors font-medium">
                    Print
                  </button>
                  {/* Fit to Page button always ON, not toggleable */}
                  <button
                    className="px-4 py-2 bg-blue-600 text-white rounded-lg font-medium cursor-default shadow"
                    disabled
                    aria-pressed="true"
                    title="Fit to Page is always enabled"
                  >
                    Fit to Page
                  </button>
                </div>
              </div>
            </div>

            {/* PDF Viewer Container */}
            <div className="flex-1 bg-white rounded-2xl shadow-lg border border-gray-200 overflow-hidden">
              <iframe
                src="/notespdf.pdf"
                title="Lesson PDF"
                className="w-full h-full"
                style={{
                  minHeight: 'calc(100vh - 220px)',
                  border: 'none',
                  background: '#ffffff',
                  width: '100%',
                  height: '100%',
                  // Always fit to page: objectFit is not supported on iframe, but we ensure full width/height
                }}
              />
            </div>
          </motion.div>
        ) : (
          <motion.div
            key="empty"
            className="h-full flex flex-col items-center justify-center p-8"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: 20 }}
            transition={{ duration: 0.3 }}
          >
            <div className="bg-white rounded-3xl shadow-xl p-12 text-center max-w-md border border-gray-100">
              <div className="w-20 h-20 bg-gradient-to-br from-blue-100 to-indigo-100 rounded-full flex items-center justify-center mx-auto mb-6">
                <svg
                  className="w-10 h-10 text-blue-600"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={1.5}
                    d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.746 0 3.332.477 4.5 1.253v13C19.832 18.477 18.246 18 16.5 18c-1.746 0-3.332.477-4.5 1.253"
                  />
                </svg>
              </div>
              <h3 className="text-2xl font-bold text-gray-900 mb-3">
                Ready to Learn?
              </h3>
              <p className="text-gray-600 mb-6 leading-relaxed">
                Select a subject from the sidebar and choose a lesson to start your learning journey.
              </p>
              <div className="flex items-center justify-center gap-2 text-sm text-gray-500">
                <div className="w-2 h-2 bg-blue-400 rounded-full animate-pulse"></div>
                <span>Waiting for selection...</span>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}

// Main Study App
export default function StudyApp() {
  // State for expanded subjects and lessons
  const [expandedSubjects, setExpandedSubjects] = useState<{ [key: string]: boolean }>({});
  const [expandedLesson, setExpandedLesson] = useState<string | null>(null);
  const [activeSubject, setActiveSubject] = useState<string | null>(null);

  // Toggle subject dropdown
  const toggleSubject = (subject: string) => {
    setExpandedSubjects((prev) => {
      const newState = Object.keys(prev).reduce((acc, key) => ({ ...acc, [key]: false }), {});
      return { ...newState, [subject]: !prev[subject] };
    });
    setActiveSubject(subject);
    setExpandedLesson(null);
  };

  // Toggle lesson dropdown
  const toggleLesson = (lesson: string) => {
    setExpandedLesson((prev) => (prev === lesson ? null : lesson));
  };

  return (
    <div className="flex h-screen w-full font-inter bg-gray-50 overflow-hidden">
      {/* Sidebar with subjects and lessons */}
      <Sidebar
        subjects={subjects}
        expandedSubjects={expandedSubjects}
        toggleSubject={toggleSubject}
        expandedLesson={expandedLesson}
        toggleLesson={toggleLesson}
      />
      
      {/* Main content area */}
      <LessonContent
        subject={activeSubject}
        lesson={expandedLesson}
        expandedLesson={expandedLesson}
      />
    </div>
  );
}